import React from "react";
import "../css/itinerary.css";
import "../css/responsive.css"
import $ from 'jquery'

const Itinerary = () => {
  $('.carousel-item').eq(0).addClass('active');
  var total = document.getElementsByClassName('.carousel-item').length;
  var current = 0;

  const moveRight = () => {
    var next = current;
    current = current+1;
    setSlide(next,current);
  }
  const moveLeft = () => {
    var next=current;
    current = current -1;
    setSlide(next,current);
  }
  const setSlide = (prev,next) => {
    var slide = current;
    if(next > total - 1){
      slide = 0;
      current = 0;
    }
    if( next < 0 ){
      slide = total - 1;
      current = total - 1;
    }
            $('.carousel-item').eq(prev).removeClass('active');
            $('.carousel-item').eq(slide).addClass('active');
            setTimeout(function(){

            },800);
  } 
  
  return (
    <section id="timeline" className="timeline-section">
        <div>
        <h1 className="mb-2 timeline-title">Weekend Events</h1>
        <hr className="hr-text mb-5" data-content="§"/>
        </div>
<div className="carousel shadow-6"  data-interval="3000" data-ride="carousel">
  <div className="logo_div"></div>
  <div className="carousel__nav">
   <span id="moveLeft" className="carousel__arrow" onClick={moveLeft()}>
        <svg className="carousel__icon" width="24" height="24" viewBox="0 0 24 24">
    <path d="M20,11V13H8L13.5,18.5L12.08,19.92L4.16,12L12.08,4.08L13.5,5.5L8,11p0Z"></path>
</svg>
    </span>
    <span id="moveRight" className="carousel__arrow" onClick={moveRight()}>
      <svg className="carousel__icon"  width="24" height="24" viewBox="0 0 24 24">
  <path d="M4,11V13H16L10.5,18.5L11.92,19.92L19.84,12L11.92,4.08L10.5,5.5L16,11H4Z"></path>
</svg>    
    </span>
  </div>
  <div className="carousel-item carousel-item--1 active">
    <div className="carousel-item__image"></div>
    <div className="carousel-item__info">
      <div className="carousel-item__container">
      <p className="carousel-item__subtitle">August 6ᵗʰ 2021</p>
      <h1 className="carousel-item__title">Friday</h1>
      <div className="carousel-item__description">
      <div className="timeline-div mt-4">
        <ul className="timeline">
        <li className="left-section-control">
            <div className="direction-r">
              <div className="flag-wrapper">
                <span className="flag">Welcome Party</span>
                <span className="time-wrapper">
                  <span className="time">4:00 PM CST</span>
                </span>
              </div>
              <div className="desc">
                <a className="map-link desc" href="https://goo.gl/maps/XYi7Dfid1sx6GRWAA"><br />137 South Main Street <br /> Breckenridge, Colorado 80424 <br /><br />(on the Riverwalk)</a>

              </div>
            </div>
            
          </li>
        </ul>
      </div>
      </div>
      <div className="align-end carousel-item__subtitle">Attire: Western | Boho</div>
        </div>
    </div>
  </div>
  <div className="carousel-item carousel-item--2">
    <div className="carousel-item__image"></div>
    <div className="carousel-item__info">
    <div className="carousel-item__container">
      <p className="carousel-item__subtitle">August <span className="ord">7ᵗʰ</span> 2021</p>
      <h1 className="carousel-item__title">Saturday</h1>
      <div className="carousel-item__description">
      <div className="timeline-div mt-4">
      <ul className="timeline">
        <li>
            <div className="direction-r">
              <div className="flag-wrapper">
                <span className="flag">Ceremony</span>
                <span className="time-wrapper">
                  <span className="time">3:30 PM CST</span>
                </span>
              </div>
              <div className="desc">
              
              <p className="desc"><a className="map-link desc" href="https://goo.gl/maps/vXo5dW2kHgcmgoKd9"> <br /> BLM Road 34 0 <br /> Breckenridge, CO 80424</a></p>
              <p className="desc">Shuttle will be available to Ten mile stations</p>
              </div>
            </div>
          </li>

          <li className="mt-5">
            <div className="direction-r">
              <div className="flag-wrapper">
                <span className="flag">Reception</span>
                <span className="time-wrapper">
                  <span className="time">To Follow</span>
                </span>
              </div>
              <div className="desc">
              <p className="desc"><a className="map-link desc" href="https://goo.gl/maps/rLvgxQhT7pBFuVy97"> <br /> 34 Marksberry Way <br /> Breckenridge, CO 80424</a></p>
              </div>
            </div>
          </li>
        </ul>
      </div>
      </div>
      <div className="align-end carousel-item__subtitle">Attire: Semi-Formal</div>
        </div>
    </div>
  </div>
  <div className="carousel-item carousel-item--3">
    <div className="carousel-item__image"></div>
    <div className="carousel-item__info">
    <div className="carousel-item__container">
      <p className="carousel-item__subtitle">August <span className="ord">8ᵗʰ</span> 2021</p>
      <h1 className="carousel-item__title">Sunday</h1>
      <div className="carousel-item__description">
      <div className="timeline-div mt-4">
      <ul className="timeline">
        <li>
            <div className="direction-r">
              <div className="flag-wrapper">
                <span className="flag">Farewell Brunch</span>
                <span className="time-wrapper">
                  <span className="time">11:00 AM CST</span>
                </span>
              </div>
              <div className="desc">
              
              <p className="desc"><a className="map-link desc" href="https://goo.gl/maps/d1AyZmDWSbi1hvDE9"> <br /> Sevens, 1979 Ski Hill Rd <br /> Breckenridge, CO 80424</a></p>
              <p className="desc">Take the scenic route up the gondola to brunch at the Sevens <br /> <br /> <a href="https://goo.gl/maps/YrR8bhpi4HUMZVJNA" className="map-link">  (Parking available at Gondola base)</a></p>
              </div>
            </div>
          </li>
        </ul>
      </div>
      </div>
      <div className="align-end carousel-item__subtitle">Attire: Casual</div>
        </div>
    </div>
  </div>
</div>
</section>
  );
};
export default Itinerary;
